<template>
  <div>
    <Doorname :citylist='citylist' ></Doorname>
  </div>
</template>
<script>
import Doorname from "@/components/doorname";
import Common from "@/utils/common.js";
export default {
  data() {
    return {
      citylist: "",
      storeId: "",
    };
  },
  components: { Doorname },
  methods: {},
  onShow() {
    let that = this;
    // 加载门店列表
    Common.findStoreList().then(res => {
      that.citylist = res.data;
    });
  }
};
</script>


<style lang="scss" scoped>
</style>





